 html {
    font-size: 16px;
  }
  
  .pets-wrap {
    background: white;
    overflow: hidden;
  }
  
  .pets-sect {
    position: relative;
  }
  
  .pets-heading {
    margin-bottom: 50px;
    text-align: center;
  }
  .pets-heading .title {
    margin-bottom: 10px;
    font-size: 2.25em;
  }
  .pets-heading .icon-palm {
    display: inline-block;
    width: 27px;
    height: 27px;
    vertical-align: 0%;
    margin-right: 10px;
    margin-left: -27px;
    background: url("/INSEBWeb/html/CM/bobe/img/pets/icon-palm.png") no-repeat 50% 50%;
    background-size: contain;
  }
  .pets-heading .descr {
    position: relative;
    display: inline-block;
    min-width: 440px;
    margin: 0 auto;
    padding: 8px 1em;
    border-radius: 40px;
    background: #45ba78;
    color: white;
    font-size: 1.25em;
    font-weight: 700;
  }
  .pets-heading.is-bub .descr:before {
    content: '';
    display: block;
    width: 20px;
    height: 20px;
    position: absolute;
    right: 40px;
    bottom: 100%;
    margin-bottom: -1px;
    background: url("/INSEBWeb/html/CM/bobe/img/pets/icon-bubarrow.png") no-repeat;
    background-size: contain;
  }
  @media (max-width: 800px) {
    .pets-heading .title {
      font-size: 1.625em;
    }
    .pets-heading .icon-palm {
      width: 20px;
      height: 20px;
    }
    .pets-heading .descr {
      min-width: 240px;
      max-width: 480px;
      font-size: 1em;
    }
    .pets-heading.is-bub .descr:before {
      margin-bottom: -7px;
    }
  }
  @media (max-width: 520px) {
    .pets-heading .title {
      margin-bottom: 10px;
      font-size: 1.4375em;
    }
    .pets-heading .descr {
      font-size: 0.9375em;
    }
  }
  
  .pets-wrap .text-green {
    color: #45ba78;
  }
  
  .pets-wrap .text-lightgreen {
    color: #9ecf89;
  }
  
  .pets-wrap .text-darkgreen {
    color: #2baa62;
  }
  
  .pets-wrap .text-deepgreen {
    color: #14894f;
  }
  
  .pets-wrap .text-num {
    margin-left: .05em;
    margin-right: .05em;
    font-family: "Trebuchet MS","Microsoft JhengHei",Arial,sans-serif;
    font-weight: 700;
    letter-spacing: -0.03em;
  }
  
  .pets-sect-kv {
    height: 480px;
  }
  .pets-sect-kv > .bg {
    position: absolute;
    left: 50%;
    margin-left: -960px;
    top: 0;
    width: 1920px;
    height: 100%;
    background: url("/INSEBWeb/html/CM/bobe/img/pets/kv.png") no-repeat 50% 50%;
  }
  .pets-sect-kv > .container {
    position: relative;
  }
  .pets-sect-kv .slogan {
    padding: 100px 0 0 120px;
    font-size: 3.125em;
    color: #333;
    letter-spacing: 0.08em;
  }
  .pets-sect-kv .slogan .sup {
    font-weight: 300;
  }
  .pets-sect-kv .slogan .sub {
    font-size: 0.48em;
    font-weight: 700;
  }
  @media (max-width: 1280px) {
    .pets-sect-kv > .bg {
      margin-left: -1100px;
    }
    .pets-sect-kv .slogan {
      padding-left: 30px;
      font-size: 2.625em;
    }
  }
  @media (max-width: 800px) {
    .pets-sect-kv {
      height: 300px;
    }
    .pets-sect-kv > .bg {
      width: 840px;
      margin-left: -420px;
      background-image: url("/INSEBWeb/html/CM/bobe/img/pets/kv-md.png");
      background-size: auto 100%;
    }
    .pets-sect-kv .slogan {
      padding: 20px 0 0 0;
      text-align: center;
      font-size: 1.875em;
    }
    .pets-sect-kv .slogan .sub {
      font-size: 0.58333em;
    }
  }
  @media (max-width: 520px) {
    .pets-sect-kv {
      height: 250px;
    }
    .pets-sect-kv > .bg {
      width: 700px;
      margin-left: -350px;
    }
    .pets-sect-kv .slogan {
      padding: 20px 0 0 0;
    }
  }
  @media (max-width: 375px) {
    .pets-sect-kv .slogan {
      font-size: 1.75em;
    }
  }
  
  .pets-sect-need {
    margin-top: -30px;
    margin-bottom: 6em;
  }
  
  .needlist {
    padding-top: 10.25641%;
  }
  .needlist:before, .needlist:after {
    content: "";
    display: table;
  }
  .needlist:after {
    clear: both;
  }
  .needlist {
    zoom: 1;
  }
  
  .needlist-inner {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .needlist-item.n1 .text {
    background: #fff6d5;
  }
  
  .needlist-item.n2 .text {
    background: #ffe9f1;
  }
  
  .needlist-item.n3 .text {
    background: #e5f8e1;
  }
  
  .needlist-item.n4 .text {
    background: #e5f7ff;
  }
  
  .needlist-item {
    position: relative;
    width: 25%;
    float: left;
    list-style: none;
    padding: 0;
    margin: 0;
    padding: 0 7px;
  }
  .needlist-item .text {
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
    overflow: hidden;
    border-radius: 100%;
    font-weight: 700;
  }
  .needlist-item .pic {
    position: absolute;
    left: 10%;
    bottom: 0;
    padding-bottom: 71.79487%;
    width: 58.11966%;
    margin: 0;
  }
  .needlist-item .pic img {
    width: 100%;
    height: auto;
  }
  .needlist-item .descr {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    padding: 30.76923% 1em 0 1.25em;
    font-size: 1.125em;
  }
  .needlist-item .price {
    position: absolute;
    left: -5%;
    top: 64.95726%;
    width: 110%;
    height: 100%;
    border-radius: 100%;
    background: #45ba78;
    padding-top: 1.6em;
    color: white;
    font-size: 1.3125em;
    line-height: 1em;
    text-align: center;
  }
  .needlist-item .price strong {
    font-size: 1.52381em;
    color: yellow;
    line-height: 0.65625em;
    vertical-align: -5%;
  }
  .needlist-item [class*="icon-stroke"] {
    position: absolute;
    right: 10px;
    top: -5px;
    width: 27.35043%;
    height: 0;
    padding-top: 27.35043%;
    border-radius: 100%;
    background: white no-repeat 50% 50%;
    background-size: auto 70%;
  }
  .needlist-item .icon-stroke-needle {
    background-image: url("/INSEBWeb/html/CM/bobe/img/pets/icon-stroke-needle.png");
  }
  .needlist-item .icon-stroke-milkbottle {
    background-image: url("/INSEBWeb/html/CM/bobe/img/pets/icon-stroke-milkbottle.png");
  }
  .needlist-item .icon-stroke-hospital {
    background-image: url("/INSEBWeb/html/CM/bobe/img/pets/icon-stroke-hospital.png");
  }
  .needlist-item .icon-stroke-rip {
    background-image: url("/INSEBWeb/html/CM/bobe/img/pets/icon-stroke-rip.png");
  }
  .needlist-item .icon-flycash {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    top: -20px;
    width: 60px;
    height: 51px;
    background: url("/INSEBWeb/html/CM/bobe/img/pets/icon-flycash.png") no-repeat;
    background-size: contain;
  }
  
  @media (max-width: 1280px) {
    .pets-sect-need {
      margin-top: -10px;
    }
  
    .needlist-item .descr {
      font-size: 1em;
      line-height: 1.4;
    }
  
    .needlist-item .price {
      font-size: 1.125em;
    }
  }
  @media (max-width: 1024px) {
    .pets-sect-need {
      margin-top: 0;
    }
  
    .needlist-item .descr {
      font-size: 0.9375em;
    }
  
    .needlist-item .price {
      font-size: 1em;
    }
  
    .needlist-item .icon-flycash {
      width: 42px;
      height: 35px;
      top: -15px;
    }
  }
  @media (max-width: 800px) {
    .pets-sect-need {
      margin-top: 1em;
      margin-bottom: 4em;
    }
  
    .needlist {
      padding-top: 5em;
    }
  
    .needlist-inner {
      width: 420px;
      margin: 0 auto;
    }
  
    .needlist-item {
      width: 210px;
    }
  
    .needlist-item:nth-child(1), .needlist-item:nth-child(2) {
      margin-bottom: 5em;
    }
  
    .needlist-item .price {
      font-size: 0.9375em;
    }
  }
  @media (max-width: 360px) {
    .needlist-item .descr {
      font-size: 0.875em;
    }
  }
  @media (max-width: 432px) {
    .needlist-inner {
      width: 210px;
    }
  
    .needlist-item {
      width: 210px;
      margin: 0 !important;
    }
  }
  @media (max-width: 432px) {
    .needlist-inner > .needlist-item {
      display: none;
    }
    .needlist-inner > .needlist-item.n1 {
      display: block;
    }
  }
  .needlist-inner .slick-list, .needlist-inner .slick-slide {
    overflow: visible;
  }
  .needlist-inner .slick-dots {
    display: none;
    position: static;
    margin: 1em 0 0 0;
    padding: 0;
    list-style: none;
  }
  .needlist-inner .slick-dots button {
    display: none;
  }
  .needlist-inner .slick-dots li {
    margin: 0 5px;
    padding: 0;
    list-style: none;
    width: 10px;
    height: 10px;
    border-radius: 10px;
    background: #eee;
    transition: background .2s;
  }
  .needlist-inner .slick-dots li.slick-active {
    background: #9ecf89;
  }
  @media (max-width: 432px) {
    .needlist-inner .slick-dots {
      display: block;
    }
  }
  
  .pets-sect-usp {
    height: 680px;
  }
  .pets-sect-usp > .bg {
    position: absolute;
    left: 50%;
    margin-left: -1500px;
    top: 80px;
    width: 3000px;
    height: 100%;
    background: url("/INSEBWeb/html/CM/bobe/img/pets/usp-bg.png") no-repeat 50% 50%;
  }
  .pets-sect-usp > .container {
    position: relative;
  }
  .pets-sect-usp.pets-sect-usp--lite {
    height: 530px;
  }
  .pets-sect-usp.pets-sect-usp--lite > .bg {
    background: url("/INSEBWeb/html/CM/bobe/img/pets/usp-bg-flat.png") no-repeat 50% 50%;
  }
  
  .usplist {
    width: 640px;
    display: flex;
    flex-wrap: wrap;
    align-items: stretch;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-right: 0;
    margin-left: auto;
  }
  
  .usplist-item {
    display: block;
    width: 33.3%;
    flex: 0 0 auto;
    list-style: none;
    padding: 0;
    margin: 0;
    padding: 2em .2em 2em .2em;
    text-align: center;
  }
  .usplist-item .descr {
    font-size: 1.125em;
    font-weight: 700;
  }
  .usplist-item [class*="icon-stroke"] {
    display: block;
    width: 56px;
    height: 56px;
    margin: 0 auto .4em;
    background: no-repeat 50% 50%;
    background-size: contain;
  }
  .usplist-item .icon-stroke-holdmoney {
    background-image: url("/INSEBWeb/html/CM/bobe/img/pets/icon-stroke-holdmoney.png");
  }
  .usplist-item .icon-stroke-traindog {
    background-image: url("/INSEBWeb/html/CM/bobe/img/pets/icon-stroke-traindog.png");
  }
  .usplist-item .icon-stroke-chip {
    background-image: url("/INSEBWeb/html/CM/bobe/img/pets/icon-stroke-chip.png");
  }
  .usplist-item .icon-stroke-piggybank {
    background-image: url("/INSEBWeb/html/CM/bobe/img/pets/icon-stroke-piggybank.png");
  }
  .usplist-item .icon-stroke-notepad {
    background-image: url("/INSEBWeb/html/CM/bobe/img/pets/icon-stroke-notepad.png");
  }
  .usplist-item .icon-stroke-catangel {
    background-image: url("/INSEBWeb/html/CM/bobe/img/pets/icon-stroke-catangel.png");
  }
  
  .usplist-item:nth-child(1), .usplist-item:nth-child(2), .usplist-item:nth-child(3) {
    border-bottom: 1px solid #e5e6e6;
  }
  
  .usplist-item:nth-child(1), .usplist-item:nth-child(2), .usplist-item:nth-child(4), .usplist-item:nth-child(5) {
    border-right: 1px solid #e5e6e6;
  }
  
  @media (max-width: 1280px) {
    .usplist-item .descr {
      font-size: 1em;
    }
  }
  @media (max-width: 1024px) {
    .pets-sect-usp > .bg {
      margin-left: -1440px;
    }
  
    .usplist {
      width: 560px;
    }
  
    .usplist-item .descr {
      font-size: 0.9375em;
    }
  }
  @media (max-width: 800px) {
    .pets-sect-usp {
      height: auto;
      margin-bottom: 4em;
    }
  
    .pets-sect-usp > .bg {
      display: none;
    }
  
    .usplist {
      width: 500px;
      margin-right: auto;
    }
  }
  @media (max-width: 520px) {
    .usplist {
      width: auto;
    }
  
    .usplist-item {
      width: 50%;
      border: 0 !important;
      padding-top: 1em;
      padding-bottom: 1em;
    }
  
    .usplist-item:nth-child(1), .usplist-item:nth-child(2), .usplist-item:nth-child(3), .usplist-item:nth-child(4) {
      border-bottom: 1px solid #e5e6e6 !important;
    }
  
    .usplist-item:nth-child(1), .usplist-item:nth-child(3), .usplist-item:nth-child(5) {
      border-right: 1px solid #e5e6e6 !important;
    }
  }
  @media (max-width: 360px) {
    .usplist-item .descr {
      font-size: 0.875em;
    }
  }
  @media (max-width: 320px) {
    .usplist {
      margin-left: -10px;
      margin-right: -10px;
    }
