  .pets-sect-form {
    margin-bottom: 4em;
  }
  
  .pets-sect-result {
    padding-top: 4em;
    padding-bottom: 4em;
    margin-bottom: 4em;
    background: #cdebdd;
    box-shadow: inset 0px 32px 32px -32px rgba(0, 0, 0, 0.1), inset 0px -32px 32px -32px rgba(0, 0, 0, 0.1), inset 0px 2px 4px -4px rgba(0, 0, 0, 0.4), inset 0px -2px 4px -4px rgba(0, 0, 0, 0.4);
  }
  
  .pets-sect-result:after {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: -1px;
    margin: 0 auto;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 40px 60px 0 60px;
    border-color: white transparent transparent transparent;
  }
  
  .pets-sect-result--no-arrow:after {
    display: none;
  }
  
